import { useEffect, useState } from "react";

function Month(props) {
  const { month, year } = props.match.params;
  const [pictures, setPictures] = useState([]);

  const getPictures = async () => {
    const resp = await fetch("./data.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });

    const list = await resp.json();

    const funTimePictures = await list.years
      .filter(funTime => funTime.year == year)[0]
      .pictures.filter(picture => picture.month == month);

    return funTimePictures;
  };

  useEffect(async () => {
    setPictures(await getPictures());
  }, [props.match.params]);

  return (
    <div>
      {pictures.map(picture => (
        <img key={picture.path} src={picture.path} />
      ))}
    </div>
  );
}

export default Month;
